    <div class="row justify-content-center">
      <div class="col-lg-3 order-lg-2">
        <div class="card-profile-image">
          <a href="#">
            <img :src="userInfo.userAvatar" class="rounded-circle">
          </a>
        </div>
      </div>
    </div>

    <div class="card-header text-center border-0 pt-8 pt-md-4 pb-0 pb-md-4">
      <div class="d-flex justify-content-between">
<!--        <a href="#" class="btn btn-sm btn-info mr-4">Connect</a>-->
<!--        <a href="#" class="btn btn-sm btn-default float-right">Message</a>-->
      </div>
    </div>

    <div class="card-body pt-4">
      <div class="text-center">
        <h5 class="h3">
          {{ userInfo.name }}<span class="font-weight-light"> </span>
        </h5>
        <div class="h5 font-weight-300">
          <i class="mr-2 ni ni-hat-3"></i>{{userInfo.level.toUpperCase()}}
        </div>
        <div class="h5 mt-4">
          <i class="ni business_briefcase-24 mr-2"></i>Access: {{userInfo.access.toUpperCase()}}
        </div>
        <div v-if="showExpiry">
          <i class="ni education_hat mr-2"></i>Expires: {{expiryDate}}
        </div>
