<script>
  import moment from 'moment';

export default {
  props: {
    userInfo: Object
  },
  computed: {
    showExpiry () {
      return this.userInfo.expiry && ['basic', 'premium'].includes(this.userInfo.access)
    },
    expiryDate () {
      return moment(this.userInfo.expiry).locale('us').format("dddd, MMMM Do YYYY")
    }
  }
};
</script>
