<template>
  <stats-card title="Get the Most Out of ChinesePod"
              type="success"
              :sub-title="subtitle"
              :icon="`fas fa-exclamation`">
    <template slot="footer">
      <base-progress v-if="percentComplete" animated striped type="success" :height="20" :value="percentComplete" label="Profile Completion"></base-progress>
      <div class="row">
        <div v-show="!started  && !finished" class="col-12">
          <div class="row">
            <div class="mt-2" :class="twoRows ? 'col-12' : 'col-6'">
              <base-button block type="success" size="sm" aria-pressed="true" @click="startOnboarding()"><i class="ni ni-single-02"></i><span class="d-block d-md-block">Edit</span></base-button>
            </div>
            <div class="mt-2" :class="twoRows ? 'col-12' : 'col-6'">
              <base-button block type="danger"  size="sm" aria-pressed="true" @click="finish()">Not Now</base-button>
            </div>
          </div>
        </div>
        <div v-show="started && inbetween" class="col-12">
          <div class="row">
            <div class="col-12 col-md-6 mt-2">
              <base-button block type="success" aria-pressed="true" @click="proceed()">Next Question</base-button>
            </div>
            <div class="col-12 col-md-6 mt-2">
              <base-button block type="danger" aria-pressed="true" @click="finish()">Done for Now</base-button>
            </div>
          </div>
        </div>
        <div v-show="started && !inbetween && !finished" class="col-12">
          <div class="row">
            <div v-show="!loading" class="col-12">

<!--              Single Selection Questions-->
              <div v-if="!currentQuestion.multipleChoice && !currentQuestion.branching" class="row">
                <div v-for="answer in currentQuestion.answers" :key="answer.id" class="col-12 col-md-6 mt-2">
                  <base-button type="secondary" block :title="answer.description" @click="singleChoice(answer)" v-html="answer.label.toUpperCase()"></base-button>
                </div>
              </div>

<!--              Multiple Selection Questions-->
              <div v-else-if="currentQuestion.multipleChoice" class="row">
                <div v-for="answer in currentQuestion.answers" :key="answer.id" class="col-12 col-md-6 mt-2">
                  <base-button :type="picked[answer.id] ? 'primary' : 'secondary'" block :title="answer.description" @click="multipleChoice(answer)" v-html="answer.label.toUpperCase()"></base-button>
                </div>
                <div class="col-12 mt-2">
                  <base-button type="success" block title="Confirm Selection" @click="multipleChoiceConfirm()">Confirm</base-button>
                </div>
              </div>

<!--              TODO Branching Questions-->

            </div>
            <div v-show="loading" class="col-12">
              <div class="text-center">
                <i class="fas fa-spinner fa-spin mx-auto my-auto"></i>
              </div>
            </div>
          </div>
        </div>
        <div v-show="finished" class="col-12">
          <div class="text-center">
            <i class="fas fa-spinner fa-spin mx-auto my-auto"></i>
          </div>
        </div>
      </div>
    </template>
  </stats-card>
</template>

<script>
  export default {
    name: "Onboarding",
    props: {
      onboardingData: Object
    },
    computed: {
      questionsLeft () {
        return this.onboardingData.questions.length;
      },
      percentComplete () {
        return parseInt((this.answerCount / this.questionCount) * 100)
      },
      twoRows: () => window.innerWidth < 768 || window.innerWidth >= 1200 && window.innerWidth < 1400
    },
    data () {
      return {
        started: false,
        finished: false,
        inbetween: false,
        loading: false,
        answerCount: 0,
        questionCount: 0,
        subtitle: 'Fill Your Profile',
        picked: {},
        currentQuestion: {}
      }
    },
    async mounted() {
      this.subtitle = `Your Profile is ${Math.floor(this.onboardingData.completeness * 100)}% Complete`;
      this.questionCount = this.questionsLeft;
      await this.$store.dispatch('logEvent', {
        action: 'show_onboarding',
        'event_category': 'engagement',
        'event_label': 'user_profile'
      });
    },
    methods: {
      async singleChoice (answer) {
        await this.markAnswer(answer.id)
      },
      multipleChoice (answer) {
        if(!this.currentQuestion.selected) {
          this.currentQuestion.selected = [];
        }
        this.picked[answer.id] = !this.picked[answer.id];
        this.$forceUpdate();
        if(this.currentQuestion.selected.includes(answer.id)) {
          this.currentQuestion.selected = this.currentQuestion.selected.filter((item) => item !== answer.id)
        } else {
          this.currentQuestion.selected.push(answer.id)
        }
      },
      async multipleChoiceConfirm () {
        await this.markAnswer(this.currentQuestion.selected)
      },
      loadQuestion () {
        if (this.questionsLeft) {
          this.currentQuestion = this.onboardingData.questions.shift();

          if(this.currentQuestion.multipleChoice) {
            this.picked = {};
            this.currentQuestion.answers.forEach(answer => {
              this.picked[answer.id] = false
            })
          }

          this.subtitle = this.currentQuestion.question
        }
      },
      startOnboarding () {
        this.loadQuestion();
        this.started = true;
      },
      async markAnswer (answer) {
        this.loading = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/onboarding/questions', {
          questionId: this.currentQuestion.key,
          answer: answer
        })
        .catch(() => {});
        this.answerCount += 1;
        await this.$store.dispatch('logEvent', {
          action: 'answer_question',
          'event_category': 'engagement',
          'event_label': 'user_profile'
        });
        if (this.questionsLeft) {
          this.inbetween = true;
          this.subtitle = 'Time for one more?';
        } else {
          this.finish()
        }
        this.loading = false
      },
      proceed () {
        this.loadQuestion();
        this.inbetween = false;
        this.$store.commit('RESET_ONBOARDING_ASK_TIMES')
      },
      async finish () {
        this.finished = true;
        this.inbetween = false;
        this.subtitle = 'Thank you for your time!';
        this.$emit('finished', true);
      },
      answerSelected(answerId) {
        return this.currentQuestion.selected && this.currentQuestion.selected.includes(answerId)
      }
    }
  }
</script>

<style scoped>
  .progress-label > span {
    /*display: none;*/
    color: white !important;
  }
</style>
