<template>
  <div class="upgrade-wrapper">
    <wistia-player
      :video-id="videoId"
      :options="options"
      @play="started = true"
      @pause="pause"
      @end="end"
    ></wistia-player>
    <a href="#" @click="upgradeClick" class="upgrade-text">
      Want to enjoy Chinesepod without ads?<br>
      Click <strong><u>here</u></strong> to upgrade to Premium!
    </a>
  </div>
</template>

<script>
  import WistiaPlayer from '@/components/Player/WistiaPlayer'

  export default {
    name: 'ContentAds',
    components: { WistiaPlayer },
    props: {
      videoId: {
        type: String,
        required: true
      }
    },
    computed: {
      upgradeLink () {
        return this.$store.state.userInfo.upgrade.upgradeLink
      }
    },
    data() {
      return {
        started: false,
        options: {
          controlsVisibleOnLoad: false,
          playSuspendedOffScreen: false,
          volumeControl: false,
          playbackRateControl: false,
          silentAutoPlay: 'allow',
          settingsControl: false,
          qualityControl: false,
          playbar: false,
          playButton: false,
          smallPlayButton: false,
          fullscreenButton: false,
          volume: 1,
          muted: 0,
          plugin: {
            share: {
              channels: ''
            }
          }
        }
      }
    },
    methods: {
      pause(video) {
        const elements = document.getElementsByClassName('w-play-pause-notifier');
        for (let element of elements) {
          element.style.display = 'none'
        }
        video.play()
      },
      end() {
        this.$emit('end')
      },
      async upgradeClick () {
        await this.$store.dispatch('logEvent', {
          action: 'click_preroll_ad',
          'event_category': 'advertising',
          'event_label': `Clicked Preroll Ad ${this.videoId}`
        });
        window.location.href = this.upgradeLink
      }
    },
    mounted () {
      setTimeout(() => {
        if (!this.started) {
          this.$emit('end')
        }
      }, 10000)
      this.$emit('play')
    }
  }
</script>

<style scoped>
  .upgrade-wrapper {
    position: relative;
  }


  .upgrade-text {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 1rem;
    padding: 1.2rem;
    width: 100%;
    flex-basis: 100%;
    background: rgba(0, 0, 0, 0.4);
    text-align: center;
    color: #fff;
  }
</style>
