  $cpod-newbie: #2487C1 !default;
  $cpod-elementary: #35C567 !default;
  $cpod-preint: #F7B500 !default;
  $cpod-intermediate: #FF4D0F !default;
  $cpod-upperint: #E1001E !default;
  $cpod-advanced: #89006B !default;
  $cpod-media: #003041 !default;

  .any {
    color: $cpod-any;
  }
  .newbie {
