  }
  .elementary {
    color: $cpod-elementary;
  }
  .preint {
    color: $cpod-preint;
  }
  .intermediate {
    color: $cpod-intermediate;
  }
  .upperint {
    color: $cpod-upperint;
  }
  .advanced {
    color: $cpod-advanced;
  }
  .bg-newbie {
