<template>
  <div>
    <base-alert v-if="isLiveStream" type="primary" dismissible>
      <span class="alert-inner--icon"><i class="fas fa-exclamation"></i></span>
      <span class="alert-inner--text"><strong> We're Live!</strong> ChinesePod is streaming live! </span><a class="btn-link text-white" href="https://youtube.com/channel/UCRY8eBLd9tPFw5-JY7S7O8Q/live"><i class="fab fa-youtube"></i> Click Here to Watch Now!</a>
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </base-alert>
    <suggestion-notification v-if="courses.suggestions" :course-data="courses.courses" @dismiss="scheduleSuggestions(Date.now())"/>
  </div>
</template>

<script>
  import {mapState, mapMutations} from 'vuex'

  import SuggestionNotification from "./Notifications/SuggestionNotification";

  export default {
    name: "NotificationArea",
    components: {SuggestionNotification},
    computed: {
      showFeatureUpdates: () => {return new Date() < new Date('2020-02-11')},
      ...mapState({
        showDashboardSwitch: 'showDashboardSwitch',
        isLiveStream: 'isLiveStream',
        showSuggestions: 'showSuggestions'
      })
    },
    data () {
      return {
        courses: {}
      }
    },
    async mounted() {

      if(new Date(this.showSuggestions) < new Date()) {
        this.courses = await this.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/get-suggestions')
          .then(({data}) => data)
          .catch((err) => {});
        if (!this.courses.suggestions) {
          this.scheduleSuggestions(Date.now())
        }
      }
    },
    methods: {
      ...mapMutations({
        scheduleSuggestions: 'SCHEDULE_SUGGESTIONS'
      })
    }
  }
</script>

<style scoped>
  .golden {
    color: gold;
  }

</style>
