<template>
  <card>
    <div slot="header" class="row align-items-center">
      <div class="col-8">
        <h3 class="mb-0">Edit profile </h3>
      </div>
      <div class="col-4 text-right">
        <base-button type="success" class="btn btn-sm" @click.prevent="updateProfile" :loading="loadingProfile">Update</base-button>
      </div>
    </div>

    <form @submit.prevent="updateProfile">
      <h6 class="heading-small text-muted mb-4">User information</h6>

      <div class="pl-lg-4">
        <div class="row">
          <div class="col-lg-6">
            <base-input
              type="email"
              label="Email address"
              placeholder="mike@email.com"
              v-model="userSettings.email"
            >
            </base-input>
          </div>
<!--          <div class="col-lg-6">-->
<!--            <base-input-->
<!--              prepend-icon="ni ni-lock-circle-open"-->
<!--              label="Password"-->
<!--              placeholder="Password"-->
<!--              type="password"-->
<!--              v-model="userSettings.password">-->
<!--            </base-input>-->
<!--          </div>-->

        </div>
        <div class="row">
          <div class="col-lg-6">
            <base-input
              type="text"
              label="Username"
              placeholder="Username"
              v-model="userSettings.username"
            >
            </base-input>
          </div>
          <div class="col-lg-6">
            <base-input
              type="text"
              label="Full Name"
              placeholder="Full Name"
              v-model="userSettings.name"
            >
            </base-input>
          </div>
        </div>
      </div>

    </form>
  </card>
</template>
<script>
  import swal from 'sweetalert2';
  import {mapState} from 'vuex';

  export default {
    data() {
      return {
        userSettings: {},
        loadingProfile: false
      };
    },
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    async beforeMount() {
      this.loadingProfile = true;
      await this.$store.dispatch('getUserInfo');
      this.loadingProfile = false;
      const settings = ['email','name', 'username'];
      settings.forEach(option => {
        this.userSettings[option] = this.userInfo[option]
      });
    },
    methods: {
      async updateProfile() {
        this.loadingProfile = true;
        await this.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/account/settings/update', this.userSettings)
          .catch((err) => swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: 'Your email and username have to be unique!',
            footer: '<a href="mailto:support@chinesepod.com">Why do I have this issue?</a>'
          }));
        await this.$store.dispatch('getUserInfo');
        this.loadingProfile = false;
      }
    }
  };
</script>
