<template>
  <div>
    <content-ads
      v-if="adsShow && showAds"
      :video-id="adsVideoId"
      @play="adsStarted"
      @end="adsStopped"
    ></content-ads>
    <lesson-media
      v-else
      :lesson-data="lessonData"
      :next-item="nextItem"
      @studied="$emit('studied')"
      @finished="$emit('finished')"
      @proceed="$emit('proceed')"/>
  </div>
</template>

<script>
  import ContentAds from "./ContentAds";
  import LessonMedia from "./MediaPlayer";
  import {mapState} from "vuex";

  export default {
    name: "Media",
    components: {LessonMedia, ContentAds},
    props: {
      lessonData: Object,
      nextItem: Boolean,
      adsDuration: {
        type: Number,
        defaultsTo: 0
      }
    },
    computed: {
      adsDurationMs() {
        return this.adsDuration * 1000
      },
      adsVideoId() {
        // if (this.adsDuration <= 30) {
        //   return '5zjgjjd2xq'
        // }
        const ads = this.$store.state.userInfo.upgrade.prerollAdId;
        if (Array.isArray(ads)) {
          return ads[Math.floor(Math.random() * ads.length)]
        } else {
          return ads
        }
      },
      showAds() {
        return this.userInfo.access === 'free'
      },
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    data () {
      return {
        adsShow: true
      }
    },
    methods: {
      end() {
        if (this.adsDuration > 0) {
          this.adsShow = true;
          this.adsPostRoll = true;
        } else {
          this.$emit('end')
        }
      },
      async adsStarted(video) {
        await this.$store.dispatch('logEvent', {
          action: 'show_preroll_ad',
          'event_category': 'advertising',
          'event_label': `Play Preroll Ad ${this.adsVideoId}`
        });
        // this.adsVideo = video
        //
        // setTimeout(() => {
        //   this.adsVideo.remove()
        //   this.adsStopped()
        // }, this.adsDurationMs)
      },
      adsStopped() {
        this.adsShow = false;

        if (this.adsPostRoll) {
          this.adsPostRoll = false;
          this.$emit('end')
        }
      }
    }
  }
</script>

