<template>
  <div>
    <div class="header pt-6 pb-6 d-flex align-items-center profile-header">
      <!-- Mask -->
      <span class="mask bg-gradient-default opacity-8"></span>
      <!-- Header container -->
      <div class="container-fluid d-flex align-items-center">
        <div class="row">
          <div class="col-lg-7 col-md-10">
            <h1 class="display-2 text-white">Hello {{ userInfo.username }}</h1>
            <p class="text-white mt-0 mb-5">This is your billing page. You can change update your payment information here and retrieve receipts.</p>
          </div>
        </div>
      </div>
    </div>

    <div class="container-fluid mt--6">
      <div class="row">
        <div class="col-xl-4 order-xl-2">
          <user-card :user-info="userInfo"></user-card>
        </div>
        <div class="col-xl-8 order-xl-1">
          <subscription-info :subscription-data="subscriptionInfo"/>
          <transaction-info :transaction-data="transactionInfo" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {mapState} from 'vuex';

  import UserCard from './UserProfile/UserCard.vue';
  import SubscriptionInfo from "./UserProfile/SubscriptionInfo";
  import TransactionInfo from "./UserProfile/TransactionInfo";

  export default {
    components: {
      TransactionInfo,
      SubscriptionInfo,
      UserCard
    },
    data () {
      return {
        subscriptionInfo: false,
        transactionInfo: false
      }
    },
    computed: {
      ...mapState({
        userInfo: 'userInfo'
      })
    },
    async mounted() {
      this.loading = true;
      await Promise.all([
        this.axios.get(process.env.VUE_APP_ROOT_API + "/api/v1/account/subscription/subscriptions")
          .then(({data}) => this.subscriptionInfo = data)
          .catch(() => {}),
        this.axios.get(process.env.VUE_APP_ROOT_API + "/api/v1/account/subscription/transactions")
          .then(({data}) => this.transactionInfo = data)
          .catch(() => {})
      ]);
      this.loading = false
    }
  };
</script>
