<template>
  <router-view>
    <notifications></notifications>
  </router-view>
</template>

<script>

  import socketIOClient from 'socket.io-client';
  import sailsIOClient from 'sails.io.js';

  export default {
    name: 'app',

    async created () {
      document.addEventListener(
        'swUpdated', this.showRefreshUI, { once: true }
      );
      if ('serviceWorker' in navigator) {
        navigator.serviceWorker.addEventListener(
          'controllerchange', () => {
            if (this.refreshing) return;
            this.refreshing = true;
            window.location.reload();
          }
        );
      }
      let io = sailsIOClient(socketIOClient);
      io.sails.url = process.env.VUE_APP_ROOT_API + '/';
      io.socket.on('NEW_LESSON', (data) => {
        this.$notification.show('New Lesson Published', {
          body: data.title,
          badge: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          icon: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          image: 'https://www.chinesepod.com/home/img/icons/icon_192.png'
        }, {
          onclick: () => {
            this.$router.push(`/lesson/${data.slug}`)
          }
        });
        this.$notify({
          verticalAlign: 'top',
          horizontalAlign: 'right',
          title: 'Lesson Published:',
          icon: 'fas fa-exclamation',
          message: `${data.title}${data.level ? ' - ' + data.level.toUpperCase(): ''} \<br\/\>Click here to open it now.`,
          timeout: 30000,
          showClose: false,
          onClick: (event, state) => {
            this.$router.push(`/lesson/${data.slug}`)
          }
        });
      });
      io.socket.on('NEW_LIVESTREAM', (data) => {
        this.$notification.show('ChinesePod is Live', {
          body: 'Watch the Live Stream',
          badge: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          icon: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          image: 'https://www.chinesepod.com/home/img/icons/icon_192.png'
        }, {
          onclick: () => {
            window.open(data.link, '_blank')
          }
        });
        this.$notify({
          type: 'danger',
          verticalAlign: 'top',
          horizontalAlign: 'right',
          title: 'ChinesePod is Live',
          icon: 'fab fa-youtube',
          message: `Click here to watch it now.`,
          timeout: 120000,
          showClose: false,
          onClick: (event, state) => {
            window.open(data.link, '_blank')
          }
        });
      });
      io.socket.on('ANNOUNCEMENT', (data) => {
        this.$notification.show(data.title, {
          body: data.body,
          badge: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          icon: 'https://www.chinesepod.com/home/img/icons/icon_192.png',
          image: 'https://www.chinesepod.com/home/img/icons/icon_192.png'
        }, {
          onclick: () => {
            if (data.link) {
              window.open(data.link, '_blank')
            }
          }
        });
        this.$notify({
          verticalAlign: 'top',
          horizontalAlign: 'right',
          title: data.title,
          icon: 'fab fa-exclamation',
          message: data.body,
          onClick: (event, state) => {
            if (data.link) {
              window.open(data.link, '_blank')
            }
          }
        });
      });
    },
    computed: {
      userInfo: {
        get () {
          return this.$store.state.userInfo
        }
      }
    },
    methods: {
      showRefreshUI (e) {
        this.registration = e.detail;
        this.notify()
      },
      refreshApp () {
        if (!this.registration || !this.registration.waiting) { return; }
        this.registration.waiting.postMessage('skipWaiting');
      },
      notify() {
        this.$notify({type: 'primary', verticalAlign: 'bottom', horizontalAlign: 'center', message: 'New version available! Click here to update!', clickHandler: this.refreshApp() });
      }
    },
    data () {
      return {
        refreshing: false,
        registration: null
      }
    }
  }
</script>
